  --loss: #ef4444;
  --loss-dim: rgba(239, 68, 68, 0.08);
  --radius-sm: 3px;
  --radius-md: 6px;
  --radius-lg: 10px;
  --transition: 140ms ease;
  --space-1: 4px;
