@keyframes ticker-scroll {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-50%);
  }
}
.live-dot {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: var(--gain);
  animation: live-pulse 2.5s ease-in-out infinite;
}
@keyframes live-pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.35;
  }
}
:focus-visible {
  outline: 2px solid var(--accent);
  outline-offset: 3px;
  border-radius: var(--radius-sm);
}
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
::-webkit-scrollbar-track {
  background: transparent;
}
::-webkit-scrollbar-thumb {
  background: rgba(255,255,255,0.12);
  border-radius: 3px;
}
::-webkit-scrollbar-thumb:hover {
  background: rgba(255,255,255,0.22);
}
.tabular-nums {
  font-variant-numeric: tabular-nums;
  font-feature-settings: "tnum";
}
@media (max-width: 768px) {
  .hide-mobile {
    display: none !important;
  }
  .show-mobile {
    display: flex !important;
  }
  .container {
    padding-left: var(--space-5);
    padding-right: var(--space-5);
  }
}
@media (min-width: 769px) {
  .show-mobile {
    display: none !important;
  }
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  background-color: #080a0f;
  color: #e8eaf0;
  font-family: 'Inter', sans-serif;
  overflow-x: hidden;
}
.glass {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.07);
  backdrop-filter: blur(20px);
}
.glass-maroon {
  background: rgba(139, 0, 48, 0.07);
  border: 1px solid rgba(139, 0, 48, 0.18);
  backdrop-filter: blur(20px);
}
.gradient-text-maroon {
  background: linear-gradient(135deg, #ffffff 0%, #e06f88 45%, #8b0030 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.label-text {
  text-transform: uppercase;
  letter-spacing: 0.15em;
  font-size: 10px;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.4);
}
@keyframes ticker-scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}
.ticker-track {
  display: flex;
  animation: ticker-scroll 50s linear infinite;
  width: max-content;
}
@keyframes pulse-dot {
  0%, 100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.4);
    opacity: 0.7;
  }
}
.pulse-dot {
  animation: pulse-dot 2s ease-in-out infinite;
}
@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-12px);
  }
}
.float-orb {
  animation: float 6s ease-in-out infinite;
}
.float-orb-slow {
  animation: float 9s ease-in-out infinite;
}
@keyframes shimmer {
  0% {
    background-position: -200% center;
  }
  100% {
    background-position: 200% center;
  }
}
.shimmer-border {
  background: linear-gradient(
    90deg,
    rgba(139, 0, 48, 0) 0%,
    rgba(139, 0, 48, 0.4) 50%,
    rgba(139, 0, 48, 0) 100%
  );
  background-size: 200% auto;
  animation: shimmer 3s linear infinite;
}
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
::-webkit-scrollbar-track {
  background: #080a0f;
}
::-webkit-scrollbar-thumb {
  background: rgba(139, 0, 48, 0.4);
  border-radius: 3px;
}
::-webkit-scrollbar-thumb:hover {
  background: rgba(139, 0, 48, 0.7);
}
